// GL Footer

footer {
	background: lighten($gl-grey, 10%);

	.footer-container {

		.footer-content-wrapper {
			height: 90px;

			@media screen and (max-width: $break-small) {
				float: left;
				height: 100%;
				padding-top: 25px;
				padding-bottom: 25px;
			}

			p {
				@include vertical-align();

				@media screen and (max-width: $break-small) {
					width: 100%;
					top: 0;
					transform: none;
				}
			}

			p.copyright {
				margin-left: 3rem;
				color: lighten($gl-grey, 40%);

				@media screen and (max-width: $break-small) {
					width: 100%;
					margin: 15px 0 0 0;
				}
			}

			p.terms {

				a {
					color: lighten($gl-grey, 40%);
					font-weight: 300;
					@include transition-property(color);
					@include transition-duration(0.2s);

					&:hover {
						color: lighten($gl-grey, 50%);
						text-decoration: none;
						@include transition-property(color);
						@include transition-duration(0.2s);
					}
				}

				@media screen and (max-width: $break-small) {
					width: 100%;
					margin: 15px 0 0 0;
				}
			}
		}
	}
}