// GL Course Search

section.course-search {
	margin: 4rem 0;

	h3.cs-search {
		margin: 0 0 3rem 0;
	}

	p.cs-results-title {

	}

	.cs-form-row {
		margin-bottom: 1rem;
	}

	.cs-form-group {
		margin-top: 3rem;
	}

	.gl-slider {
		.gl-slider-title {
			color: $gl-grey;
		}

		.gl-slider-placeholder {
			display: block;
			height: 15px;
			border: 1px solid lighten($gl-grey, 90%);
			@include border-radius(4px);
		}
	}

	.cs-breaker {
		margin: 4rem 0;
		height: 1px;
		background: lighten($gl-grey, 90%);
	}

	.cs-feature {

		.cs-feature-title {
			margin-bottom: 2rem;
			color: $gl-grey;
		}

		.cs-feature-item {
			
			span {
				margin-left: 5px;
				color: $gl-green;
			}
		}
	}

	.course-results {
		padding: 3rem;
		border: 1px solid lighten($gl-grey, 90%);
		@include border-radius(4px);

		@media screen and (max-width: $break-small) {
			margin-top: 3rem;
			padding: 2rem;
		}

		.cr-label-left {
			text-align: left;
		}
	}
}

// Course and resutls cards

.gl-card {
	margin-top: 3rem;
	padding: 3rem;
	@include box-shadow(lighten($gl-grey, 85%) 0px 4px 16px);

	@media screen and (max-width: $break-small) {
		padding: 2rem;
	}

	&:first-child {
		margin-top: 0;
	}

	.gl-card-title {
		font-size: 1.8rem;
		color: $gl-green;

		p {
			margin-bottom: 0;
		}
	}

	.gl-card-rating-wrapper {
		@media screen and (max-width: $break-small) {
			margin-top: 1rem;
		}
	}

	.gl-card-rating {
		padding-top: 4px;	
		font-size: 1.2rem;

		p {
			margin-bottom: 0;

			span {
				font-weight: 600;
			}
		}
	}

	ul.gl-card-stars {
		padding-left: 1rem;
		margin-bottom: 0;

		@media screen and (max-width: $break-small) {
			padding-left: 0rem;
		}

		li {
			display: inline-block;
		}
	}

	p.gl-card-text {
		margin: 1.5rem 0;
		font-weight: 200;
	}

	.gl-card-pills {
		margin: 1rem 0;

		ul {
			padding: 0;
			margin: 0;
			list-style: none;

			li {
				margin-right: 1rem;
				padding: 0.8rem 1rem;
				float: left;
				border: 1px solid $gl-green;
				@include border-radius(4px);
				color: $gl-green;

				@media screen and (max-width: $break-small) {
					margin-bottom: 1rem;
				}

				&.gl-pill-red {
					border: 1px solid $gl-pink;
					color: $gl-pink;
				}
			}
		}
	}

	.gl-card-price-cap {
		margin: 1rem 0;
		color: $gl-green;
		font-size: 1.6rem;

		span {
			font-weight: 600;
		}
	}

	.gl-card-location {
		margin: 1rem 0;

		span {
			color: $gl-grey;
			font-weight: 600;
		}

		&.gl-card-location-alt {
			margin: 0 0 0.5rem 0;
		}
	}

	.gl-card-map-link {

		span {
			margin-right: 5px;
		}
	}

	.gl-card-cta-wrapper {
		margin: 3rem 0 0 0;

		@media screen and (max-width: $break-medium) {
			img {
				width: 100%;
			}
		}

		@media screen and (max-width: $break-small) {
			img, form {
				margin-bottom: 3rem;
			}

			img {
				width: 50%;
			}
		}

		&.gl-card-cta-wrapper-alt {
			margin: 1rem 0 0 0;
		}
	}
}