// GL Dashboard

nav.db-nav {
	background: lighten($gl-grey, 90%);
	box-shadow: none;

	ul.nav-db {

		li {
			margin-left: 30px;
			a {
				padding: 15px 0 23px 0;
				&:hover {
					border-bottom: 2px solid $gl-green;
					@include transition(border-bottom .1s ease);
				}
			}
		}
	}

	.db-dropdown-menu {

		a {
			padding: 5px 12px;
			
			span {
				margin-right: 8px;
			}
		}
	}

	a.dropdown-toggle {
		span {
			margin-left: 10px;
		}
	}

	.profile-image {
		margin-top: 5px;
		margin-right: 15px;
		float: right;
		width: 40px;
		height: 40px;

		img {
			@include border-radius(4px);
		}
	}

	input.db-input {
		border: 0;
		box-shadow: none;
	}

	.db-dropdown-toggle {
		padding: 15px 15px 15px 0;
	}
}

.db-wrapper {
	padding: 4rem 0;

	h1.db-title {
		margin-bottom: 4rem;
		color: $gl-grey;
		font-size: 2.8rem;
	}

	.db-tabs {
		li {
			&.active {
				a {
					color: $gl-green;
				}
			}

			a {
				color: lighten($gl-grey, 50%);
			}
		}
	}
}

.db-tab-pane {
	padding: 4rem 0;
}

.db-btn {
	line-height: inherit;
}

table.db-table {
	tbody {
		tr {
			td {
				padding: 14px 8px;
				vertical-align: middle;
			}
		}
	}
}

.db-footer {
	position: absolute;
	width: 100%;
	bottom: 0;
}