// GL Buttons

.gl-btn {
	// height: 50px;
	border: 0;
	color: white;
	background-color: $gl-orange;
	text-transform: uppercase;
	line-height: 3.5rem;
	@include transition(background .2s ease);

	&:hover {
		background-color: darken($gl-orange, 10%);
		@include transition(background .2s ease);
		color: white;
	}

	&.gl-btn-sm {
		line-height: 2.5rem;
	}

	&.gl-btn-min-width {
		min-width: 185px;

		@media screen and (max-width: $break-small) {
			width: 100%;
		}
	}

	&.gl-btn-super-min-width {
		min-width: 260px;
	}

	&.gl-btn-shadow {
		@include box-shadow(lighten($gl-grey, 85%) 0px 4px 16px);
	}

	&.gl-btn-classy {
		background-color: transparent;
		border: 1px solid $gl-orange;
		color: $gl-orange;
		@include transition(background .2s ease);

		&:hover {
			background-color: $gl-orange;
			color: white;
			@include transition(background .2s ease);
		}

		&.gl-btn-classy-green {
			color: $gl-green;
			border: 1px solid $gl-green;

			&:hover {
				background-color: $gl-green;
				color: white!important;
			}
		}

		&.gl-btn-classy-blue {
			color: $gl-blue;
			border: 1px solid $gl-blue;

			&:hover {
				background-color: $gl-blue;
				color: white;
			}
		}

		&.gl-btn-classy-purple {
			color: $gl-purple;
			border: 1px solid $gl-purple;

			&:hover {
				background-color: $gl-purple;
				color: white;
			}
		}

		&.gl-btn-classy-pink {
			color: $gl-pink;
			border: 1px solid $gl-pink;

			&:hover {
				background-color: $gl-pink;
				color: white;
			}
		}

		&.gl-btn-classy-grey {
			color: lighten($gl-grey, 50%);
			border: 1px solid lighten($gl-grey, 50%);

			&:hover {
				background-color: lighten($gl-grey, 40%);
				color: white;
			}
		}

		&.gl-btn-classy-light-grey {
			color: lighten($gl-grey, 70%);
			border: 1px solid lighten($gl-grey, 70%);

			&:hover {
				background-color: lighten($gl-grey, 60%);
				color: white;
			}
		}
	}

	&.gl-btn-green {
		background-color: $gl-green;

		&:hover {
			background-color: darken($gl-green, 10%);
			color: white;
		}
	}

	&.gl-btn-blue {
		background-color: $gl-blue;

		&:hover {
			background-color: darken($gl-blue, 10%);
			color: white;
		}
	}

	&.gl-btn-purple {
		background-color: $gl-purple;

		&:hover {
			background-color: darken($gl-purple, 10%);
			color: white;
		}
	}

	&.gl-btn-pink {
		background-color: $gl-pink;

		&:hover {
			background-color: darken($gl-pink, 10%);
			color: white;
		}
	}

	&.gl-btn-grey {
		background-color: lighten($gl-grey, 50%);

		&:hover {
			background-color: lighten($gl-grey, 40%);
		}
	}

	&.gl-btn-white {
		background-color: white;
		color: $gl-green;

		&:hover {
			background-color: lighten($gl-grey, 90%);
		}
	}

	&.gl-btn-hero {
		width: 220px;
		height: 45px;
		border: 0;
		background: $gl-orange;
		color: white;
		text-transform: uppercase;
		@include border-radius(4px);
		font-size: 16px;
		font-weight: 600;
		line-height: 3.3rem;
		@include box-shadow(lighten($gl-grey, 85%) 0px 4px 16px);
		@include transition(background .2s ease);

		&:hover {
			background-color: darken($gl-orange, 10%);
			@include transition(background .2s ease);
		}
	}

	&.gl-btn-facebook {
		background-color: $gl-facebook-blue;
		@include transition(background .2s ease);

		i {
			margin-right: 5px;
		}

		&:hover {
			background-color: darken($gl-facebook-blue, 10%);
			@include transition(background .2s ease);

		}
	}

	&.gl-btn-google {
		background-color: $gl-google-red;
		@include transition(background .2s ease);

		i {
			margin-right: 5px;
		}

		&:hover {
			background-color: darken($gl-google-red, 10%);
			@include transition(background .2s ease);

		}
	}
}

.gl-btn-login {
	line-height: inherit;
	min-width: 110px;
	margin-left: 1.4rem;
	color: $gl-green;
	border: 1px solid $gl-green;
	text-transform: uppercase;
	font-size: 13px;
	@include transition(background .2s ease);

	@media screen and (max-width: $break-small) {
		margin: 30px 0 0 0;
		display: block;
		width: 100%;
		min-height: 45px;
		font-size: 1.5rem;
		line-height: 3rem;
	}

	&:hover {
		background: $gl-green;
		color: white!important;
		@include transition(background .2s ease);
		border: 1px solid $gl-green;
	}
}

.gl-btn-wrapper {
	margin: 3rem 0;

	&.homepage-hero-btn {

	}
}

a.gl-simple-link {
	color: lighten($gl-grey, 50%);
	@include transition(color .2s ease);
	border-bottom: 1px solid lighten($gl-grey, 50%);
	text-transform: uppercase;
	font-size: 1.2rem;

	&.gl-simple-link-bold {
		font-size: 1.4rem;
		font-weight: 600;
	}

	&.gl-simple-link-green {
		color: $gl-green;
		border-bottom: 1px solid $gl-green;

		&:hover {
			color: darken($gl-green, 10%);
		}
	}

	&.gl-simple-link-orange {
		color: $gl-orange;
		border-bottom: 1px solid $gl-orange;

		&:hover {
			color: darken($gl-orange, 10%);
		}
	}

	&.gl-simple-link-purple {
		color: $gl-purple;
		border-bottom: 1px solid $gl-purple;

		&:hover {
			color: darken($gl-purple, 10%);
		}
	}

	&.gl-simple-link-lg {
		font-size: 1.6rem;
	}

	&:hover {
		color: lighten($gl-grey, 40%);
		@include transition(color .2s ease);
		text-decoration: none;
	}
}

.gl-btn-spacer {
	margin-top: 4rem;
}