// GL Corporate

section.corporate {

	.gl-corporate-hero-btn {

		margin-bottom: 5rem;
	}
}

section.corporate-content {

	.corporate-section-wrap {

		h4 {
			margin-top: 0;
			margin-bottom: 2rem;
			font-size: 3rem;
			font-weight: 200;
			line-height: 4rem;
			color: $gl-grey;
		}

		p {
			font-weight: 200;
			font-size: 1.5rem;
			line-height: 1.6em;

			@media screen and (max-width: $break-small) {
				margin-bottom: 3rem;
			}
		}

		img {
			@include border-radius(4px);
		}
	}

	p.call-our-rep {
		font-size: 2.4rem;
		font-weight: 200;

		span {
			color: $gl-green;
			font-weight: 600;
		}

		@media screen and (max-width: $break-small) {
			margin-bottom: 5rem;
		}
	}
}