// GL Accordion

.gl-accordion {
	@media screen and (max-width: $break-small) {
		margin-top: 2rem;
	}

	&.gl-accordion-alt {
		@media screen and (max-width: $break-small) {
			margin-top: 0rem;
			margin-bottom: 5rem;
		}
	}

	.gl-panel-group-alt {
		padding: 0;
		margin: 0;
	}

	.gl-panel-alt {
		padding: 0;
		border: 0;
	}

	.gl-panel-heading-alt {
		padding: 1.5rem 0;
		background: white;
		border-bottom: 1px solid $gl-green;

		a {
			color: $gl-green;

			&:hover, &:active, &:focus {
				text-decoration: none!important;
			}
		}
	}

	.gl-panel-body-alt {
		padding: 1.5rem 0;
		border: 0!important;
	}
}