// GL Homepage

section.logo-banner {
	background: $gl-grey;
	padding: 2rem;
	color: lighten($gl-grey, 50%);

	@media screen and (max-width: $break-small) {
		padding: 3rem 0;
	}

	p {
		margin: 0;
	}

	img.partner-logo {
		margin: 0;

		@media screen and (max-width: $break-small) {
			margin-top: 3rem;
		}
	}
}

section.why-book {
	background: $gl-blue;
	color: white;

	h3 {
		margin: 8rem 0;
	}

	.stats-wrapper {
		padding: 4rem;
		background-color: white;
		@include border-radius(4px 4px 0 0);
		@include box-shadow(darken($gl-blue, 8%) 8px -10px 20px);
		color: $gl-grey;

		@media screen and (max-width: $break-medium) {
			@include border-radius(4px 4px 4px 4px);
		}

		.stats-row-minus {
			margin-top: -1.8rem;

			@media screen and (max-width: $break-small) {
				margin-top: 2rem;
			}
		}

		.stats-number {
			border-radius: 50%;
			height: 100px;
			width: 100px;
			border: 1px solid $gl-green;
			color: $gl-green;

			@media screen and (max-width: $break-small) {
				margin-left: auto;
				margin-right: auto;
			}

			p {
				margin-top: 2.6rem;
				font-size: 3rem;
			}

			&.stats-number-blue {
				border: 1px solid $gl-blue;
				color: $gl-blue;
			}

			&.stats-number-orange {
				border: 1px solid $gl-orange;
				color: $gl-orange;
			}
		}

		.stats-explain {
			@media screen and (max-width: $break-small) {
				margin-top: 2rem;
				text-align: center;
			}

			p {
				color: $gl-green;
				font-size: 1.6rem;
				font-weight: 600;

				&.stats-explain-blue {
					color: $gl-blue;
				}

				&.stats-explain-orange {
					color: $gl-orange;
				}
			}
		}
	}

	.usp-wrapper {
		@media screen and (max-width: $break-medium) {
			margin: 4rem 0;
		}

		.usp-tick {
			border-radius: 50%;
			height: 50px;
			width: 50px;
			background: white;
			color: $gl-green;
			@include box-shadow(darken($gl-blue, 10%) -3px 3px 30px);
			padding-top: 1.3rem;

			span {
				margin-top: 3rem;
				font-size: 2.4rem;
			}

			@media screen and (max-width: $break-small) {
				margin-top: 2rem;
				margin-left: auto;
				margin-right: auto;
			}
		}

		.usp-text {
			@media screen and (max-width: $break-small) {
				margin-top: 2rem;
				text-align: center;
			}

			p {
				margin: 0;
				font-size: 2.2rem;
				color: white;
				font-weight: 200;
			}
		}

		.row-usp {
			margin-top: 2.6rem;
		}

		.refund-link {
			margin-top: 3rem;

			@media screen and (max-width: $break-small) {
				text-align: center;
			}

			a {
				padding-bottom: 0.4rem;
				color: white;
				border-bottom: 1px solid white;

				&:hover {
					text-decoration: none;
				}
			}
		}
	}
}

section.popular-courses {

	h3 {
		margin: 8rem 0;
		color: lighten($gl-grey, 50%);
	}

	.pop-course-wrapper {
		margin: 0 0 8rem 0;

		&.pop-course-btns {
			margin: 4rem 0 8rem 0;
		}

		img {
			margin: 0 0 4rem 0;
		}

		p.pop-course-title {
			margin: 0 0 2rem 0;
			color: $gl-grey;
			font-size: 2.4rem;
		}

		p.pop-course-text {
			margin: 0 0 4rem 0;
			font-weight: 200;
			line-height: 2.5rem;
		}
	}
}

