	.support-wrapper {
		margin-top: 8rem;

		.support-img-wrapper {
			@include border-radius(4px);
			@include box-shadow(lighten($gl-grey, 90%) 10px -10px 20px);

			img {
				@include border-radius(4px 4px 0 0);

				@media screen and (max-width: $break-small) {
					margin-bottom: 4rem;
				}
			}
		}

		.cf-list-group-wrapper {
			@media screen and (max-width: $break-small) {
				margin-top: 4rem;
				margin-bottom: 8rem;
			}
		}

		.cf-list-group {
			margin-bottom: 3rem;

			.cf-tickbox {
				width: 40px;
				height: 40px;
				background-color: $gl-green;
				color: white;
				@include box-shadow(lighten($gl-grey, 90%) -3px 3px 30px);
				@include border-radius(4px);
				padding-top: 9px;

				span {
					font-size: 2rem;
				}
			}

			.cf-tickbox-text {
				font-size: 2rem;
				font-weight: 200;

				@media screen and (min-width: $break-medium) {
					margin-left: 2rem;
				}

				p {
					margin: 0;
				}
			}
		}
	}
}

section.corporate-package {

	h3 {
		margin: 8rem 0;
	}

	.corp-wrapper {
		margin-bottom: 8rem;

		.cp-text-wrapper {

			p.cp-title {
				margin: 0 0 2rem 0;
				color: $gl-grey;
				font-size: 4rem;
				font-family: 'Hind', sans-serif;
				font-weight: 300;

				@media screen and (max-width: $break-small) {
					text-align: center;
				}
			}

			p.cp-text {
				margin: 0 0 5rem 0;
				font-size: 1.8rem;
				font-weight: 200;
				line-height: 3rem;

				@media screen and (max-width: $break-small) {
					text-align: center;
				}
			}

			.cp-btn {
				@media screen and (max-width: $break-small) {
					text-align: center;
				}
			}
		}

		.cp-feedback {
			padding: 3rem;
			border: 1px solid $gl-purple;
			@include border-radius(4px);

			@media screen and (max-width: $break-small) {
				margin-top: 8rem;
			}
		}

		.cp-person {

			@media screen and (max-width: $break-small) {
				margin-bottom: 4rem;
			}

			img {
				@include border-radius(4px);
			}
		}

		.cp-logo {
			margin-top: 2rem;

			@media screen and (max-width: $break-small) {
				margin-bottom: 4rem;
				max-width: 180px;
				margin-left: auto;
				margin-right: auto;
			}
		}

		.cp-content {
			@media screen and (max-width: $break-small) {
				text-align: center;
			}

			p.name {
				margin: 0 0 0 0;
				color: $gl-purple;
				font-size: 2.5rem;
			}

			p.bus-name {
				font-size: 1.8rem;
				font-weight: 200;
			}

			p.text {
				font-size: 1.2rem;
				font-weight: 200;
				line-height: 2.2rem;

				@media screen and (max-width: $break-small) {
					margin-top: 2rem;
					font-size: 1.4rem;
				}
			}

			.cara-dots {
				img {
					margin-right: 10px;

					@media screen and (max-width: $break-small) {
						margin-top: 2rem;
						margin-right: 20px;
						width: 15px;
						height: 15px;
					}
				}
			}
		}
	}
}

section.gtna {
	padding: 8rem 0;
	background-color: $gl-blue;
	color: white;

	.gtna-wrapper {

		p.title {
			font-size: 3rem;
			font-weight: 200;
		}

		p.message {
			margin: 1.8rem 0 3.5rem 0;
			font-size: 2rem;
			font-weight: 200;

			span {
				font-weight: 600;
			}
		}
	}
}

section.social {
	padding: 8rem 0;
	background: lighten($gl-grey, 35%);
	color: white;

	p.title {
		font-size: 2.1rem;
		font-weight: 200;
	}

	p.text {
		font-size: 1.6rem;
		font-weight: 200;
	}

	.social-icons {
		margin: 2.6rem 0 0 0;
		font-size: 2.4rem;

		a {
			color: white;
			margin-left: 3rem;
			@include transition(color .2s ease);

			&:hover {
				color: lighten($gl-grey, 90%);
				@include transition(color .2s ease);
			}

			&:first-child {
				margin-left: 0;
			}
		}
	}

	.news-wrapper {

		@media screen and (max-width: $break-small) {
			margin-top: 4rem;
		}		

		p.title {
			font-size: 2.1rem;
			font-weight: 200;
		}

		p.text {
			font-size: 1.6rem;
			font-weight: 200;
		}

		.news-inputs {
			margin-top: 2rem;

			input {
				border: 0;
			}
		}

		.news-btn {
			margin-top: 2rem;
		}
	}
}

// Used in multiple places

.cf-group {
	padding: 2rem 0;
	border-bottom: 1px solid lighten($gl-grey, 90%);

	&:first-child {
		padding-top: 0;
	}

	&:last-child {
		padding: 2rem 0 0 0;
		border-bottom: 0;
	}
}

.cf-text-wrapper {

	p.cf-title {
		margin: 0 0 2rem 0;
		color: $gl-grey;
		font-size: 4rem;
		font-family: 'Hind', sans-serif;
		font-weight: 300;

		@media screen and (max-width: $break-small) {
			text-align: center;
		}
	}

	p.cf-text {
		margin: 0 0 5rem 0;
		font-size: 1.8rem;
		font-weight: 200;
		line-height: 3rem;

		@media screen and (max-width: $break-small) {
			text-align: center;
		}
	}

	.cf-icons {

		@media screen and (max-width: $break-small) {
			margin-bottom: 8rem;
			text-align: center;
		}

		span {
			font-size: 3rem;
			color: $gl-green;
			margin-right: 1rem;
		}
	}
}

// Used in multiple areas

.cf-customers-wrapper {
	padding: 3rem;
	background-color: white;
	@include border-radius(4px);
	@include box-shadow(lighten($gl-grey, 90%) -10px -10px 20px);

	@media screen and (max-width: $break-small) {
		text-align: center;
	}

	img.img-person {

		@media screen and (max-width: $break-small) {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 2rem;
			max-width: 120px;
		}
	}

	p.cf-group-title {
		margin: 0 0 0.7rem 0;
		font-weight: 200;
	}

	p.cf-group-text {
		font-size: 1.2rem;
		font-weight: 200;
	}

	.cf-stars {

		img {
			margin-right: 5px;
		}
	}
}