// GL Forms

.gl-forms {
	select, input {
		// border: 0;

		&.input-shadow {
			@include box-shadow(lighten($gl-grey, 85%) 0px 4px 16px);
		}

		&.input-clean {
			box-shadow: none;
			border: 1px solid lighten($gl-grey, 90%);
		}

		&.input-wipe {
			border: 0;
		}
	}

	select {

		&.gl-select-orange {
			color: $gl-orange;

			&.gl-select-green-border {
				border: 1px solid $gl-green;
			}
		}

		&.gl-select-green {
			color: $gl-green;

			&.gl-select-green-border {
				border: 1px solid $gl-green;
			}
		}
	}

	// ::-webkit-input-placeholder {
	//    color: red;
	// }

	// :-moz-placeholder { /* Firefox 18- */
	//    color: red;  
	// }

	// ::-moz-placeholder {  /* Firefox 19+ */
	//    color: red;  
	// }

	// :-ms-input-placeholder {  
	//    color: red;  
	// }

	.gl-form-group {
		margin: 0 0 1.5rem 0;
		padding: 0;

		// &:first-child {
		// 	margin-top: 0;
		// }

		.input-icon {
			right: -5px;
			top: 10px;
		}

		&.form-group-clean {
			margin: 0;
		}
	}

	.gl-checkbox-wrap {
		margin-top: 2rem;

		span.gl-icon {
			margin: 0 10px 0 5px;
			font-size: 3.5rem;
			color: $gl-green;
		}
	}

	.gl-checkbox {
		float: left;
	}

	.gl-checkbox-top {
		margin-top: 10px;
	}

	.gl-checkbox-img {
		margin: 0 10px 0 5px;
	}

	.gl-checkbox-first {
		margin-top: 0;

		@media screen and (max-width: $break-small) {
			margin-top: 2rem;
		}
	}

	.gl-checkbox-last {
		margin-bottom: 0;
	}

	.gl-form-group-clean {
		margin-bottom: 0;
	}

	.gl-label {
		font-weight: 400;
	}
}

nav.gl-pagination {

	ul {
		margin: 3rem 0 0 0;

		&.gl-pagination-alt {
			margin: 1rem 0 0 0;
		}

		li {
			a {
				color: $gl-green;

				&:hover {
					color: darken($gl-green, 10%);
				}
			}
		}
	}

	.active {

		a {
			background-color: $gl-green;
			border: 1px solid $gl-green;

			&:hover {
				background-color: darken($gl-green, 10%);
				border: 1px solid darken($gl-green, 10%);
			}
		}
	}
}

.gl-radio {
	margin-top: 0;
	margin-bottom: 0;
}

p.form-disclaimer {
	margin-bottom: 0;
	font-size: 1.2rem;
}

.select-mob {
	@media screen and (max-width: $break-small) {
		margin-bottom: 2rem;
	}
}

p.address {
	margin-bottom: 0;
}

.gl-checkbox-margin-bottom {
	margin-bottom: 0;
}