// GL Sitemap

section.sitemap {
	padding: 6rem 0;
	background: $gl-grey-lighter;
	color: white;

	p.sitemap-header {
		padding: 0;
		margin: 0;
		font-size: 1.6rem;
		font-weight: 400;
	}

	ul.sitemap-list {
		padding: 0;
		margin: 0;

		li {
			list-style: none;
			margin-bottom: 1.2rem;

			&:first-child {
				margin-top: 1.2rem;
			}

			a {
				color: white;
				font-size: 1.3rem;
				font-weight: 300;
				@include transition-property(color);
				@include transition-duration(0.2s);

				&:hover {
					color: lighten($gl-grey, 85%);
					text-decoration: none;
					@include transition-property(color);
					@include transition-duration(0.2s);
				}
			}
		}

		.gl-panel {
			background: none;
			border: none;

			.gl-panel-heading {
				background: none;
				color: white;
				border-bottom: 0;
				padding: 0;
			}

			.gl-panel-body {
				border-top: 0!important;
				padding: 0;
			}

			.gl-panel-title {
				font-size: 1.3rem;
				font-weight: 300;
			}

			.gl-footer-accordion {
				text-decoration: none;
			}
		}

		.gl-sitemap-icon {
			margin-left: 8px;
			font-size: 1rem;
		}

		ul.footer-social-icons {
			padding: 0;
			margin: 0;

			li {
				margin: 0 1.5rem 1.5rem 0;
				float: left;
			}
		}
	}

	.footer-logos {
		height: 65px;

		p.logo {
			margin: 4rem 0 0 30px;
			@include vertical-align();

			&:first-child {
				margin-left: 0;
			}

			&.register-text {
				font-size: 1.3rem;
				font-weight: 300;

				@media screen and (max-width: $break-large) {
					margin: 0;
					width: 100%;
				}
			}
		}
	}
}