	&.homepage {
		background:
			linear-gradient(
		      rgba(0, 0, 0, 0.40), 
		      rgba(0, 0, 0, 0.40)
		    ),
		    url(images/hero-homepage.jpg);
		@include background-size(cover);
	}

	&.gl-hero-license {
		background:
			linear-gradient(
		      rgba(0, 0, 0, 0.40), 
		      rgba(0, 0, 0, 0.40)
		    ),
		    url(images/license-hero.jpg);
		@include background-size(cover);
	}

	&.gl-hero-corporate {
		background:
			linear-gradient(
		      rgba(0, 0, 0, 0.50), 
		      rgba(0, 0, 0, 0.50)
		    ),
		    url(images/corporate-hero.jpg);
		@include background-size(cover);
	}

	&.gl-hero-about-us {
		background:
			linear-gradient(
		      rgba(0, 0, 0, 0.50), 
		      rgba(0, 0, 0, 0.50)
		    ),
		    url(images/about-hero.jpg);
		@include background-size(cover);
	}

	&.gl-hero-list-course {
		background:
			linear-gradient(
		      rgba(0, 0, 0, 0.50), 
		      rgba(0, 0, 0, 0.50)
		    ),
		    url(images/list-course-hero.jpg);
		@include background-size(cover);
	}

	&.gl-hero-how-to {
		background:
			linear-gradient(
		      rgba(0, 0, 0, 0.50), 
		      rgba(0, 0, 0, 0.50)
		    ),
		    url(images/how-to-hero.jpg);
		@include background-size(cover);
	}

	// &.gl-hero-alt {
	// 	height: 200px;
	// 	background: $gl-green;
	// 	color: white;

	// 	&:after {
	// 		content: url(images/faq-hero.svg);
	// 		height: 134px;
	// 		width: 186px;
	// 		position: absolute;
	// 		bottom: 0;
