	// 	}
	// }

	.gl-hero-alt {
		height: 200px;
		position: relative;

		@media screen and (max-width: $break-small) {
			height: auto;
		}

		&:after {
			content: url(images/faq-hero.svg);
			height: 134px;
			width: 186px;
			position: absolute;
			bottom: -5px;
			right: 0;

			@media screen and (max-width: $break-small) {
				display: none;
			}
		}

		&.gl-hero-alt-location {
			&:after {
				content: url(images/location-hero.svg);
				height: 134px;
				width: 186px;
				position: absolute;
				bottom: -5px;
				right: 0;

				@media screen and (max-width: $break-small) {
					display: none;
				}
			}
		}

		&.gl-hero-alt-reviews {
			&:after {
				content: url(images/reviews-hero.svg);
				height: 134px;
				width: 186px;
				position: absolute;
				bottom: -5px;
				right: 0;

				@media screen and (max-width: $break-small) {
					display: none;
				}
			}
		}

		&.gl-hero-alt-tc {
			height: auto;
			
			h1.faq-h1 {
				margin: 0;
				padding: 4rem 0;
			}

			&:after {
				content: "";
				height: 134px;
				width: 186px;
				position: absolute;
				bottom: -5px;
				right: 0;

				@media screen and (max-width: $break-small) {
					display: none;
				}
			}
		}
	}

	&.gl-hero-contact-us {

		.hero-contact-us-wrapper {
			padding: 6rem 0;

			.hc-icon-wrapper {
				float: left;
				margin-right: 1.5rem;

				.hc-icon {
					width: 80px;
					height: 80px;
					@include border-radius(50%);
					background: white;
					@include box-shadow(darken($gl-green, 10%) 0px 4px 16px);

					span {
						display: block;
						padding-top: 20px;
						font-size: 4rem;
						color: $gl-green;
					}
				}
			}
		}
	}

	.hero-icon {
		margin-bottom: 5rem;
		font-size: 5rem;
	}

	.hero-content-wrapper {

		h1 {
			margin: 4rem 0;
			color: white;

			span {
				font-weight: 500;
			}
		}

		.search-container {
			background-color: white;
			@include border-radius(4px 4px 0 0);

			h2 {
				color: $gl-green;
				font-weight: 300;
				margin: 3rem 0 1rem 0;

				span {
					font-weight: 500;
				}
			}

			.search-header {
				@media screen and (max-width: $break-small) {
					padding: 0 2rem;
				}
			}

			.license-search, .course-search {
				background-color: lighten($gl-grey, 95%);
				padding: 4rem;
			}

			.course-search {
				background-color: lighten($gl-grey, 90%);
			}

			p.search-explain {
				margin: 0 0 3rem 0;
				color: lighten($gl-grey, 50%);

				@media screen and (max-width: $break-small) {
					margin-top: 2rem;
					padding: 0 2rem;
				}
			}

			.or-text {
				position: absolute;
				top: 45px;
				left: -10px;
				font-size: 1.8rem;

				@media screen and (max-width: $break-small) {
					top: -14px;
					left: 50%;
					margin-left: -12px;
					font-size: 2rem;
				}
			}
		}
	}
}

.gl-hero-training-provider {
	background:
		linear-gradient(
	      rgba(0, 0, 0, 0.70), 
	      rgba(0, 0, 0, 0.70)
	    ),
	    url(images/hero-t4s.jpg);
	@include background-size(cover);
	color: white;
	padding: 4rem 0;

	@media screen and (max-width: $break-small) {
		text-align: center;	
	}

	h1.tp-h1 {
		@media screen and (max-width: $break-small) {
			margin: 5rem 0 0 0;
		}
	}

	.tp-logo-wrapper {
		background: white;
		padding: 2rem;
		@include box-shadow(lighten($gl-grey, 10%) 0px 4px 16px);

		img {
			@media screen and (max-width: $break-small) {
				display: initial;
			}
		}
	}

	.tp-circle {
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 2rem;
		width: 120px;
		height: 120px;
		background: white;
		@include border-radius(50%);
		@include box-shadow(lighten($gl-grey, 10%) 0px 4px 16px);

		p {
			padding-top: 3.5rem;
			color: $gl-green;
			font-size: 3.3rem;
			font-weight: 500;
		}

		img {
			margin-top: 1.8rem;
		}

		ul.tp-list {
			margin: 0;
			padding: 3rem 0 0 0;
			list-style: none;

			li {
				display: inline-block;
				margin-right: -4px;
			}
		}
	}

	.tp-stat-wrapper {
		margin: 4rem 0 2rem 0;

		.tp-stat-text {
			@media screen and (max-width: $break-small) {
				margin-bottom: 4rem;
			}
		}
	}

	.tp-social-wrapper {
		@media screen and (max-width: $break-small) {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 2rem;
		}
	}

	.tp-social-circle {
		display: inline-block;
		margin-right: 15px;
		width: 40px;
		height: 40px;
		@include border-radius(50%);
		border: 1px solid white;
		@include box-shadow(lighten($gl-grey, 10%) 0px 2px 8px);
		background: $gl-green;

		i {
			color: white;
			margin-top: 1.3rem;
		}

		&.tp-social-circle-fb {
			background: $gl-facebook-blue;
		}

		&.tp-social-circle-tw {
			background: $gl-twitter-blue;
		}
	}

	.tp-personal-image {

		img {
			@media screen and (max-width: $break-small) {
				margin-left: auto;
				margin-right: auto;
				margin-bottom: 2rem;
				max-width: 30%;
			}
		}
	}

	.tp-hero-name {
		margin: 0 0 0 0;
		font-size: 1.8rem;
		font-weight: 200;
	}

	.tp-hero-num {
		margin: 0 0 0 0;
		font-weight: 500;
		font-size: 2.5rem;

		@media screen and (max-width: $break-small) {
			margin-bottom: 3rem;
		}
	}

	p.awarding-org {
		margin: 1rem 0 0 0;

		span {
			font-weight: 200;
		}
	}
}