// GL Login

.login-wrapper {
	padding: 8rem 0;

	p.login-header {
		color: $gl-grey;
		font-size: 2rem;
	}
}

.login-ad-wrapper {
	background:
		linear-gradient(
	      rgba(0, 0, 0, 0.40), 
	      rgba(0, 0, 0, 0.40)
	    ),
	    url(images/corporate-1.jpg);
	@include background-size(cover);
}

.login-ad-content {
	padding: 4rem;
	color: white;
	font-size: 2.5rem;

	@media screen and (max-width: $break-small) {
		padding: 4rem 0;
	}
}

.login-ad-stat {
	padding: 0 4rem 4rem 4rem;
	color: white;

	@media screen and (max-width: $break-small) {
		padding: 0 0 4rem 0;
	}

	p {
		font-size: 1.6rem;
		font-weight: 600;
		span {
			font-weight: 200;
		}
	}
}