// GL Booking

section.gl-booking {
	
	h3 {
		margin-top: 0;
	}

	.gl-booking-wrapper {
		margin: 3rem 0;

		.gl-booking-step {
			@media screen and (max-width: $break-small) {
				margin-bottom: 4rem;
			}
		
			.gl-booking-num {
				margin-left: auto;
				margin-right: auto;
				width: 50px;
				height: 50px;
				@include border-radius(50%);
				border: 1px solid lighten($gl-grey, 50%);

				&.gl-booking-num-active {
					background: $gl-green;
					color: white;
					border: 1px solid $gl-green;
					@include box-shadow(lighten($gl-grey, 85%) 0px 2px 8px);

					&.gl-booking-num-active-purple {
						background: $gl-purple;
						border: 1px solid $gl-purple;
					}
				}

				span {
					display: block;
					font-size: 2rem;
					margin-top: 1rem;
				}
			}

			.gl-booking-exp {
				font-size: 1.1rem;
				margin-top: 1rem;
			}
		}

	}

	p.gl-booking-table-header {
		margin: 3rem 0	;
		font-size: 1.6rem;

		@media screen and (max-width: $break-small) {
			margin: 3rem 0 0 0;
		}
	}

	.po-wrapper {
		margin-left: auto;
		margin-right: auto;
		max-width: 300px;
		background: lighten($gl-grey, 60%);
		@include border-radius(4px);
		color: white;

		@media screen and (max-width: $break-small) {
			margin-top: 3rem;
		}

		&.po-wrapper-green {
			background: $gl-green;
		}

		.po-title {
			font-size: 2.5rem;
			font-weight: 200;
			padding: 1rem;

			p {
				margin: 0;
			}
		}

		.po-pop {
			padding: 1rem;
		}

		.po-price {
			font-size: 4rem;
			font-weight: 500;
			padding: 1rem;

			p {
				margin: 0 0 2rem 0;
			}
		}

		ul.po-list {
			list-style: none;
			margin: 0;
			padding: 0;

			&.po-list-green {

				li {
					border-top: 1px solid darken($gl-green, 10%);

					a {

					}
				}
			}

			li {
				border-top: 1px solid lighten($gl-grey, 50%);

				a {
					display: block;
					padding: 1rem 2rem;
					color: white;

					span {
						margin-right: 5px;
					}
				}
			}
		}
	}

	.po-btn-wrapper {
		border-top: 1px solid lighten($gl-grey, 50%);
		padding: 2rem;

		&.po-btn-wrapper-green {
			border-top: 1px solid darken($gl-green, 10%);
		}
	}

	.course-selected {
		margin: 4rem 0;

		p {
			font-weight: 600;
		}

		p.course-selected-title {
			font-size: 1.6rem;
			font-weight: 200;
		}

		.course-selected-btn-wrap {
			margin: 3rem 0 0 0;
		}
	}

	.next-step-btn-wrap {
		margin-top: 3rem;
	}

	.booking-form-wrapper {
		padding: 3rem;
		@include border-radius(4px);
		border: 1px solid lighten($gl-grey, 90%);

		@media screen and (max-width: $break-small) {
			margin-top: 3rem;
		}

		.booking-form {

		}

		.gl-form-social-btn {

			@media screen and (max-width: $break-small) {
				margin-bottom: 1.2rem;
			}
		}

		.bf-highlight {
			margin-bottom: 0;

			span {
				font-size: 1.4rem;
				font-weight: 600;
				color: $gl-green;
			}
		}
	}

	.gl-form-input-space {
		@media screen and (max-width: $break-small) {
			margin-bottom: 2rem;
		}
	}

	ul.delegate-cara {
		display: inline-block;
		list-style: none;
		margin: auto;
		padding: 0;

		li {
			float: left;
			margin-left: 15px;

			&:first-child {
				margin-left: 0;
			}

			a {

			}
		}
	}

	.bf-btn-mob {
		@media screen and (max-width: $break-small) {
			margin-bottom: 2rem;
		}
	}

	.order-summary {

		.summary-wrapper {
			@media screen and (max-width: $break-small) {
				margin-bottom: 2rem;
			}

			.row {
				margin-top: 1rem;

				&:first-child {
					margin-top: 0;
				}
			}

			p.summary-title {
				font-weight: 600;
			}

			p.summary-text {
				font-weight: 200;
			}
		}
	}

	ul.summary-list {
		padding: 0 0 0 18px;
		position: relative;

		li {
			a {

			}

			p.summary-list-title {
				color: $gl-grey;
				font-size: 1.5rem;
				margin-bottom: 0.5rem;
			}

			p.summary-list-text {

			}

			.summary-cross {
				position: absolute;
				top: 0;
				right: 0;

				a {
					text-decoration: none;
					color: lighten($gl-grey, 50%);
					@include transition(color .2s ease);

					&:hover {
						color: lighten($gl-grey, 40%);
						@include transition(color .2s ease);
					}
				}
			}
		}
	}

	.summary-total {
		p.total {
			color: $gl-grey;
			margin-bottom: 0.5rem;
		}

		p.discount {
			margin-bottom: 0;
		}
	}

	.next-step-btn-wrap {

		@media screen and (max-width: $break-small) {
			text-align: center;
			width: 100%;
			display: block;
			margin-bottom: 2rem;

			&.end-btn-wrap {
				margin-bottom: 0rem;
			}

			a {
				width: 100%;
				float: left;
			}
		}
	}

	.gl-payment {

		.payment-method {
			span, p, .pm-icons {
				margin-right: 10px;
				float: left;
			}

			span {
				font-size: 2.2rem;
				color: $gl-green;
			}

			p {
				font-size: 1.7rem;
			}

			.pm-icons {

				i {
					margin-right: 5px;
				}
			}
		}

		p.pm-contact-text {
			@media screen and (max-width: $break-small) {
				margin-bottom: 0;
			}
		}
	}

	.pm-contact-wrapper {

		@media screen and (max-width: $break-small) {
			margin-top: 3rem;
		}
	}

	.booking-confirmation-wrapper {
		margin: 2rem 0;

		.booking-title-wrapper {
			margin: 0;
			
			@media screen and (max-width: $break-small) {
				margin: 2rem 0;
			}

		}

		.bc-title {
			font-weight: 600;
			color: $gl-grey;

			@media screen and (max-width: $break-small) {
				text-align: center;
			}
		}

		.bc-result {
			@media screen and (max-width: $break-small) {
				text-align: center;
			}
		}

		ul.bc-list {
			margin: 0;
			padding: 0 0 0 18px;
		}

		.total-price {
			font-weight: 600;
			font-size: 1.6rem;
			color: $gl-green;

			@media screen and (max-width: $break-small) {
				text-align: center;
			}
		}
	}

	.finish-actions {
		margin: 3rem 0 0 0;

		@media screen and (max-width: $break-small) {
			a {
				display: block;
				margin-bottom: 2rem;
			}
		}

		span {
			margin-right: 8px;
		}
	}
}