// GL Sidebar

.gl-sidebar-find {
	margin-bottom: 3rem;
	padding: 3rem;
	border: 1px solid lighten($gl-grey, 90%);
	@include border-radius(4px);

	&.gl-sidebar-find-orange {
		border: 1px solid $gl-orange;
	}

	&.gl-sidebar-find-purple {
		border: 1px solid $gl-purple;
	}

	&.gl-sidebar-find-green {
		border: 1px solid $gl-green;
	}

	p.find-course-text {
		margin-bottom: 2rem;
		font-size: 1.8rem;
		color: $gl-grey;
	}

	&.gl-sidebar-find-margin {
		margin-top: 2rem;
		margin-bottom: 0;
	}
}

.gl-sidebar-cf {
	padding: 3rem;
	border: 1px solid lighten($gl-grey, 90%);
	@include border-radius(4px);

	p.cf-title {
		font-size: 2rem;
	}

	.cf-customers-wrapper {
		padding: 0;
		box-shadow: none;
	}
}

.gl-sidebar-list {
	padding: 3rem;
	border: 1px solid lighten($gl-grey, 90%);
	@include border-radius(4px);

	ul {
		margin: 0;
		padding: 0;
		list-style: none;

		li {
			a {
				color: $gl-grey;
				display: block;
				padding: 1rem 0;
				border-bottom: 1px solid lighten($gl-grey, 90%);
				@include transition(color .2s ease);

				&.list-first {
					padding-top: 0;
				}

				&.list-last {
					padding-bottom: 0;
					border-bottom: 0;
				}

				&:hover, &:focus, &:active {
					color: $gl-green;
					text-decoration: none!important;
					@include transition(color .2s ease);
				}
			}	
		}
	}
}