// GL Typography

body {
	font-family: 'Open Sans', sans-serif;
	color: lighten($gl-grey, 50%);
}

h1, h2, h3 {
	font-family: 'Hind', sans-serif;
	font-weight: 300;
}

.gl-text-bold {
	font-weight: 600;
}

.gl-text-highlight {
	color: $gl-green;
	font-weight: 600;
	font-size: 1.6rem;
}

h4.gl-content-title {
	margin-top: 0;
	margin-bottom: 2.5rem;
	color: $gl-grey;
	font-size: 2.4rem;

	@media screen and (max-width: $break-small) {
		margin-bottom: 3.5rem;
	}

	&.gl-content-title-no-mg {
		margin-bottom: 0;
	}
}

p.gl-content-body {
	margin-top: 0;
	font-weight: 200;
	line-height: 2.5rem;

	&.gl-content-body-space {
		margin-bottom: 2rem;
	}
}

p.sidebar-header {
	font-size: 2rem;
	color: $gl-grey;
	margin-bottom: 2rem;
}

p.gl-margin-bottom {
	margin-bottom: 0;
}