// GL License

section.license {
	margin: 0 0 10rem 0;

	h1.license-h1 {
		font-size: 4rem;
		padding-top: 26rem;
		color: white;

		&.license-h1-padding {
			padding-top: 21rem;

			@media screen and (max-width: $break-small) {
				padding-top: 4rem;
			}
		}

		@media screen and (max-width: $break-small) {
			padding-top: 4rem;
		}
	}

	h3.license-h3 {
		font-size: 2.3rem;
		margin-bottom: 5rem;
		font-family: open sans, sans-serif;
		color: white;
		font-weight: 300;
		line-height: 3.5rem;

		&.license-h3-margin {
			margin-bottom: 3rem;
		}
	}

	.hero-arrow {
		margin-bottom: -30px;

		img {
			width: 60px;
			height: 60px;
			@include border-radius(50%);
			@include transition(box-shadow .2s ease);

			&:hover {
				@include box-shadow(lighten($gl-grey, 85%) 0px 4px 16px);
				@include transition(box-shadow .2s ease);
			}
		}
	}
}

section.gl-content {

	.gl-main-content {

		.gl-content-body-wrapper {
			margin-bottom: 4rem;

			&.gl-content-body-wrapper-alt {
				margin-bottom: 0;
			}
		
			// h4.gl-content-title {
			// 	margin-top: 0;
			// 	margin-bottom: 2.5rem;
			// 	color: $gl-grey;
			// 	font-size: 2.4rem;

			// 	@media screen and (max-width: $break-small) {
			// 		margin-bottom: 3.5rem;
			// 	}
			// }

			// p.gl-content-body {
			// 	margin-top: 0;
			// 	font-weight: 200;
			// 	line-height: 2.5rem;

			// 	&.gl-content-body-space {
			// 		margin-bottom: 2rem;
			// 	}
			// }

			.process-wrap {
				margin: 1.5em 0 4rem 0;

				@media screen and (max-width: $break-small) {
					margin: 0;
				}

				.process-item {

					@media screen and (max-width: $break-small) {
						margin-bottom: 3rem;
					}

					span {
						display: block;
						margin-bottom: 1rem;
						font-size: 4rem;
					}

					img {
						margin-bottom: 1rem;
					}

					p {
						margin-bottom: 0.8rem;
					}

					p.pi-exp {
						font-size: 1.1rem;
						font-weight: 200;
					}

					&.pi-bottom-mob {
						@media screen and (max-width: $break-small) {
							margin: 0;
						}
					}
				}
			}

			ul.gl-content-list {
				list-style: none;
				margin: 0 0 0 0;
				padding: 0;

				li {
					margin-bottom: 2rem;
					line-height: 2.4rem;

					&:before {
						content: "\f0a9";
						font-family: FontAwesome;
						color: $gl-orange;
						margin-right: 8px;
					}

					&.li-green {

						&:before {
							content: "\f0a9";
							font-family: FontAwesome;
							color: $gl-green;
							margin-right: 8px;
						}
					}

					&.li-purple {

						&:before {
							content: "\f0a9";
							font-family: FontAwesome;
							color: $gl-purple;
							margin-right: 8px;
						}
					}
				}
			}
		}

		p.course-book-live-feed {
			margin-bottom: 0;

			@media screen and (max-width: $break-small) {
				margin-top: 2rem;
			}

			&.course-book-live-feed-green {
				span {
					color: $gl-green;
				}
			}

			&.course-book-live-feed-purple {
				span {
					color: $gl-purple;
				}
			}

			span {
				color: $gl-orange;
				font-weight: 600;
			}
		}
	}
}