// GL Training provider

p.tp-course-search-title {
	font-size: 1.6rem;
	margin: 0;
}

.map-small {
	@include border-radius(4px);
	margin-bottom: 3rem;
}

.tp-load-more-btn {
	@media screen and (max-width: $break-small) {
		margin-bottom: 4rem;
	}

	&.tp-load-more-btn-alt {
		@media screen and (max-width: $break-small) {
			margin-bottom: 0;
		}
	}
}