//	GL Navbar

.gl-navbar {
	margin-bottom: 0;
	background: white;
	border: none;
	font-weight: 300;
	@include box-shadow($gl-grey-light 1px 1px 10px);
	z-index: 1000;

	.gl-navbar-content {
		padding: 1rem 0;

		@media screen and (max-width: $break-small) {
			padding: 0;
		}
	}

	a {
		color: $gl-grey;
		text-decoration: none;
		@include transition(color .2s ease);

		&:hover {
			color: $gl-green!important;
			@include transition(color .2s ease);
		}

		&.phone {
			margin: 1.5rem 0 0 2.4rem;
			font-size: 1.6rem;
			line-height: 2rem;
			cursor: default;

			@media screen and (max-width: $break-small) {
				display: block;
				margin: 2.5rem 0 1.5rem 0;
			}

			span {
				color: $gl-green;
				font-weight: 600;
			}

			&:hover {
				color: $gl-grey!important;
			}
		}
	}

	.gl-navbar-header {
		padding: 1rem 0;
	}

	.gl-brand {
		padding: 0.5rem 0 0 15px;

		@media screen and (max-width: $break-small) {
			padding: 3px 0 0 15px;
		}
	}

	a.fix-padding {

		@media screen and (max-width: $break-large) {
			padding-right: 0;
		}
	}

	.gl-navbar-dropdown {
		right: auto;
	}

	.gl-caret {
		margin-left: 5px;
	}

	.gl-navbar-toggle {
		margin: 5px 15px 0 0;
		border: 0;
		padding: 0;

		span.icon-menu-circle {
			font-size: 4rem;
			color: lighten($gl-grey, 65%);
		}

		&:hover {
			background: none;
		}
	}

	a.gl-mobile-phone {
		font-size: 4rem;
		margin-right: 1rem;
		color: $gl-green;
	}
}