// GL Global

.max-width {
	width: 1336px;
}

.row-clean {
	margin: 0;
}

.col-clean {
	padding: 0;
}

.valign-wrapper {
	display: flex;
	flex-direction: row;
	align-items: center;

	@media screen and (max-width: $break-small) {
		display: block;
	}
}

.gl-breaker {
	height: 1px;
	background: lighten($gl-grey, 90%);

	&.gl-breaker-margin {
		margin: 3rem 0;
	}

	&.gl-breaker-margin-lg {
		margin: 5rem 0;
	}

	&.gl-breaker-margin-alt {
		margin: 0 0 3rem 0;
	}

	&.gl-breaker-margin-alt-lg {
		margin: 0 0 5rem 0;
	}
}

.gl-section-container {
	margin: 4rem 0;
}

.row-centered {
	text-align: center;

	.col-centered {
		display:inline-block;
	    float:none;
	    text-align:left;
	    margin-right:-4px;
	}
}

.gl-col-centered {
    float: none;
    margin: 0 auto;
}

.gl-element-shadow {
	@include box-shadow(lighten($gl-grey, 85%) 0px 4px 16px);

	&.gl-element-shadow-lrg {
		@include box-shadow(lighten($gl-grey, 75%) 0px 8px 24px);
	}
}

.bf-row {
	margin-bottom: 2rem;

	&.bf-row-lg {
		margin-bottom: 5rem;
	}

	&.bf-row-md {
		margin-bottom: 3rem;
	}

	&.bf-row-sml {
		margin-bottom: 1rem;
	}
}

.gl-container-fix {
	margin-top: 70px;

	@media screen and (min-width: $break-medium) and (max-width: $break-medium) {
		margin-top: 108px;
	}

	&.gl-container-fix-lg {
		margin-top: 140px;

		@media screen and (min-width: $break-medium) and (max-width: $break-medium) {
			margin-top: 180px;
		}
	}
}

.input-clear-margin {
	margin-bottom: 0!important;

	@media screen and (max-width: $break-small) {
		margin-bottom: 2rem!important;
	}
}

.gl-map {
	// @include border-radius(4px);
	margin-bottom: 4rem;
}